import React, { Component } from 'react';

export default class About extends Component{
  render(){
    return(
      <div className="about">
        <h1>About Me</h1>
        <div class="aboutSection container">
          <div class="photo">
          <img src="https://pbs.twimg.com/profile_images/637788232164372480/5LBqC0Bb.png"/>
          </div>
          <div class="text">
            Hello, I'm Laura García Diéguez, a Primary Teacher, self-taught Web Designer and yoga teacher in process. I am 27 years young and I live in Sevilla, Spain. I dream about living in different countries while I am flourishing as a yoga teacher and a web designer. I enjoy learning new things, cooking, doing exercise, designing new things to use in my daily life, watching series and travelling.
          <br />
          <button className='btn btn-warning'>Full Resume</button>
            <div className="icons">
              <i class="devicon-html5-plain-wordmark"></i>
              <i class="devicon-css3-plain-wordmark"></i>
              <i class="devicon-javascript-plain"></i>
              <i class="devicon-jquery-plain-wordmark"></i>
              <i class="devicon-bootstrap-plain-wordmark"></i>
              <i class="devicon-sass-original"></i>
              <i class="devicon-gulp-plain"></i>
              <i class="devicon-git-plain-wordmar"></i>
              <i class="devicon-github-plain-wordmark"></i>
              <i class="devicon-react-original-wordmark"></i>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



