import React, { Component } from 'react';
import Typist from 'react-typist';


export default class Header extends Component {
  render(){
    return(
      <div id="Header" className='Header'>
        <nav className="navbar navbar-expand-sm fixed-top">
          <a className="navbar-brand" href="#Header">
            <img src="https://res.cloudinary.com/dioukzx4w/image/upload/v1507649465/PortfolioPage/Logo/logo.png"></img>
          </a>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
            <span className="fa fa-bars"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav ml-auto">
              <a className="nav-item nav-link" href="#">About Me <span className="sr-only">(current)</span></a>
              <a className="nav-item nav-link" href="#">Projects</a>
              <a className="nav-item nav-link" href="#">Contact Me</a>
            </div>
          </div>
        </nav>
        <div className="container information">
            <div className="intro">
                <Typist startDelay={2000}>
                  Hi, I am Lucas Brizolla <br />
                  Front-end Developer
                </Typist>
              <div className="social">
                <a href="https://www.linkedin.com/in/lucas-brizolla-94082650/" target="_blank"><i className="fa fa-linkedin-square fa-2x" aria-hidden="true"></i></a>
                <a href="https://github.com/LBeLD" target="_blank"><i className="fa fa-github-square fa-2x" aria-hidden="true"></i></a>
                <a href="mailto:lbbrizolla@gmail.com"><i className="fa fa-envelope fa-2x" aria-hidden="true"></i></a>
                <a href="https://www.freecodecamp.org/lbeld" target="_blank"><i className="fa fa-free-code-camp fa-2x" aria-hidden="true"></i></a>
              </div>

              <a href="#projects"><i className="fa fa-angle-double-down fa-3x" aria-hidden="true"></i></a>
          </div>
        </div>
      </div>
    );
  }
}



